<template>
  <table class="table">
    <thead>
      <tr>
        <th scope="col">#</th>
        <th scope="col">নাম</th>
        <th scope="col">Action</th>
      </tr>
    </thead>
    <tbody>
      <tr v-if="occupations === null">
        <td colspan="3">
          <preloader-component />
        </td>
      </tr>
      <tr v-else-if="occupations.length === 0">
        <td colspan="3" class="text-center">
          No union is available
        </td>
      </tr>
      <occupation-item-component
        v-else
        v-for="(occupation, index) in occupations"
        :key="index"
        :index="index"
        :occupation="occupation"
      />
    </tbody>
  </table>
</template>

<script>
import { mapState, mapActions } from "vuex";
import PreloaderComponent from "../../common/PreloaderComponent";
import OccupationItemComponent from "./OccupationItemComponent";
export default {
  name: "OccupationListComponent",
  components: { OccupationItemComponent, PreloaderComponent },
  computed: {
    ...mapState(["occupations"])
  },
  methods: {
    ...mapActions(["bindOccupations"]),
    initOccupation() {
      if (this.occupations === null) {
        this.bindOccupations();
      }
    }
  },
  mounted() {
    this.initOccupation();
  }
};
</script>

<style scoped></style>
